/*----- end FONT COLORS -----*/
section.above-the-fold {
  margin-bottom: 0px;
}
section.above-the-fold > .content-container {
    display: flex;
    justify-content: space-between;
}
.token-sale__text p.lead {
  border-left: 1px solid #EF8A11;
  padding-left: 20px;
  font-size: 16px;
  max-width: 350px;
  opacity: 0.8;
  margin-top: 20px;
}
.above-the-fold {
  border-radius: 0;
  background-image: url("../../assets/images/backgrounds/purplewavebig.png");
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 0;
  background-position-y: bottom;
}

/*video link*/
a.launch-video {
  position: relative;
}
a.launch-video img.video-link-play {
    position: absolute;
}
a.launch-video img.video-link-image {
    width: calc(100% - 60px);
    max-width: 450px;
}

/*video modal*/
.modal-content {
  background-color: transparent;
  border: none;
}
.modal-header {
  border-bottom: none;
  padding: 0;
}
.modal-body {
  padding: 0;
}
.modal-body video {
    width: 100%;
}
.modal-footer {
  display: none;
}
.close, .close:focus {
  color: #fff;
  text-shadow: 0 1px 0 #000;
  opacity: 0.7;
  outline: none;
}
.close:hover {
  color: #fff;
  text-shadow: 0 1px 0 #000;
  opacity: 1;
}

/*media mentions*/
.media-mentions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
}
.media-mentions p {
    text-align: center;
    text-transform: uppercase;
    width: 100%;
}
.media-mentions__img-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.media-mentions__img-container img {
      width: 20%;
      max-width: 200px;
}
.tokensale-background {
  position: absolute;
  min-height: 100%;
  min-width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
.cta__form {
  display: flex;
  justify-content: flex-start;
}
.purple-bkg {
  background-image: url("../../assets/images/backgrounds/ctapurple.png");
  background-size: cover;
  color: #FFF;
}
.purple-btn {
  border: none;
}
.above-the-fold .cta__form__email {
  border-radius: 20px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  height: 42px;
  border-right: none;
  font-size: 12px;
  width: 220px;
  padding-left: 20px;
}
.above-the-fold .cta__form__email:active, .above-the-fold .cta__form__email:focus {
    outline: none;
}
.above-the-fold .cta__form__submit {
  border-radius: 20px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  height: 40px;
  width: 150px;
  text-transform: uppercase;
}

/*----- MEDIA QUERIES -----*/
@media screen and (max-width: 1000px) {
video {
    width: 100%;
}
.token-sale {
    flex-direction: column;
}
.token-sale__text,
  .token-sale__video-launcher {
    width: 100%;
    margin: 20px 0;
    max-width: 100%;
    text-align: center;
}
.token-sale__text p.lead {
    margin: 20px auto;
    width: 100%;
    max-width: 560px;
}
.cta__form {
    justify-content: center;
    margin-top: 20px;
}
}
@media screen and (max-width: 500px) {
.above-the-fold .cta__form__email {
    width: calc(100% - 20px);
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
}
.above-the-fold .cta__form__submit {
    width: calc(100% - 20px);
    margin-top: 10px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
}
}



