
.t-navbar {
  display: flex;
  align-items: center;
  top: 52px;
  position: relative;
  height: 75px;
  width: 100%;
  padding: 20px 75px;
  background-color: white;
}
.t-navbar .matryx-logo {
    width: 150px;
}
.t-navbar .links {
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    justify-content: space-evenly;
}
@media screen and (max-width: 800px) {
.t-navbar {
    display: none;
}
}



