.event-item {
  width: calc(33.3333% - 40px);
  min-width: 300px;
  box-sizing: border-box;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  margin: 20px 20px;
}
.event-item__heading {
    position: relative;
    margin-bottom: 10px;
    box-sizing: border-box;
}
.event-item__heading__img {
      width: 100%;
      opacity: 0.5;
      max-height: 110px;
      height: auto;
}
.event-item__heading__text {
      width: calc(100% - 40px);
      max-height: 95px;
      background-color: #FFF;
      position: absolute;
      left: 25px;
      top: 20px;
      padding: 15px 20px;
}
.event-item__heading__name {
      color: #423176;
}
.event-item__body {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 20px;
}
.event-item__body__description {
      color: #423176;
      font-size: 16px;
      width: 100%;
}
.event-item button.btn-purple {
    height: 35px;
    width: 120px;
    font-size: 11px;
    padding: 5px 10px;
    margin-top: 20px;
}



