.news-events__news__primary {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.news-events__sectionHeader {
  font-size: 40px;
  font-weight: 700;
  color: #423176;
  padding: 25px 0;
}
.news-events__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 30px;
}
.news-events__title__sub {
    text-transform: uppercase;
    color: grey;
    background-color: white;
    border: 2px solid white;
    padding: 0 20px;
    position: relative;
    left: -20px;
}
.news-events__title__sub--events {
      color: grey;
      text-align: right;
      text-transform: uppercase;
      background-color: white;
      border: 2px solid white;
      padding: 0 20px;
}
.news-events__title__line {
    height: 15px;
    border-bottom: 1px solid grey;
    width: 100%;
}
.news-events__news__primary {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}



