
.carousel-indicators {
  position: relative;
}
.carousel-indicators li {
    height: 10px;
    width: 10px;
    border: none;
    border-radius: 50px;
    max-width: 10px;
}
.quote-text {
  opacity: 0.7;
  font-style: italic;
}
.quote-author {
  float: right;
}
.quote-author__name {
    font-weight: bold;
    display: block;
    text-align: right;
}
.quote-author__company {
    display: block;
    text-align: right;
}



