.primary-event {
  width: calc(33.3333% - 40px);
  min-width: 300px;
  box-sizing: border-box;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  margin: 20px 20px;
}
.primary-event__heading {
    height: 155px;
    position: relative;
    margin-bottom: 10px;
    box-sizing: border-box;
}
.primary-event__heading__img {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0.5;
}
.primary-event__heading__text {
      width: calc(100% - 40px);
      height: 95px;
      background-color: #FFF;
      position: absolute;
      left: 25px;
      top: 27px;
      padding: 15px 20px;
}
.primary-event__heading__name {
      color: #423176;
      font-weight: 700;
}
.primary-event__heading__date {
      color: grey;
      letter-spacing: 0.5px;
}
.primary-event__body {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 20px 30px;
}
.primary-event button.btn-purple {
    font-size: 11px;
    padding: 5px 10px;
    margin-top: 30px;
}



