.cta {
  text-align: center;
}
.white {
  color: white;
}
.cta__main {
  font-size: 26px;
  margin-bottom: 15px;
}
.cta__desc {
  font-size: 16px;
}
.cta-middle-banner {
  position: relative;
}
.cta-middle-banner .cta-banner {
    top: 35px;
    position: relative;
    padding: 80px 0;
}
section.cta-banner.white-bkg {
  background-image: url("../assets/images/backgrounds/ctawhite.png");
  background-size: cover;
  margin: -20px 0;
  background-position: center;
  padding: 80px 0px;
}
#cta-bottom.purple-bkg {
  background-image: url("../assets/images/backgrounds/ctapurplebottom.png");
  background-position: top;
  padding-top: 80px;
  background-size: cover;
}



