.matryx-team {
  background-color: #6B3FD6;
  color: #FFF;
}
.matryx-team__title,
.matryx-advisors__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.matryx-team__title__title {
  font-size: 200%;
  flex-flow: 0 1 auto;
  font-size: 200%;
}
.matryx-team__title__line {
  border-bottom: 1px solid #FFF;
  width: calc(100% - 190px);
  height: 20px;
}
.matryx-team__team {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 90px;
}
.matryx-advisors__title__title {
  font-size: 200%;
  order: 2;
}
.matryx-advisors__title__line {
  width: calc(100% - 230px);
  border-bottom: 1px solid white;
  align-self: center;
}

/*----- MEDIA QUERIES -----*/
@media screen and (max-width: 500px) {
.matryx-team__team, .matryx-team__advisors,
  .matryx-advisors__team,
  .matryx-advisors__advisors {
    margin-top: 20px;
}
.matryx-team__title,
  .matryx-advisors__title {
    justify-content: center;
}
.matryx-team__title__line,
    .matryx-advisors__title__line {
      display: none;
}
.member {
    margin: 25px 6px;
}
}



